import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Dashboard from './views/Dashboard.vue'
import About from './views/About.vue'
import Articles from './views/Articles.vue'
import Users from './components/Admins/Users.vue'
import Characters from './components/Admins/Characters.vue'
import Games from './components/Admins/Games.vue'
import ManageArticle from './components/Admins/ArticlesMan.vue'
import ViewUser from './components/Admins/ViewUser.vue'
import AddUser from './components/Admins/AddUser.vue'
import EditUser from './components/Admins/EditUser.vue'
import AddArticle from './components/Admins/AddArticle.vue'
import AddChar from './components/Admins/AddCharacter.vue'
import AddGame from './components/Admins/AddGame.vue'
import ViewChar from './components/Admins/ViewChar.vue'
import ViewGame from './components/Admins/ViewGame.vue'

Vue.use(Router)

export default new Router({
  mode: 'history', //this is to remove the hash in address bar but it will be a problem if it ported to online servers
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      path: '/articles',
      name: 'articles',
      component: Articles
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/users',
      name: 'users',
      component: Users,
    },
    {
      path: '/char',
      name: 'characters',
      component: Characters,
    },
    {
      path: '/games',
      name: 'games',
      component: Games
    },
    {
      path: '/games/add_game',
      name: 'add-game',
      component: AddGame
    },
    {
      path: '/man_articles',
      name: 'manage-articles',
      component: ManageArticle
    },
    {
      path: '/articles/add_article',
      name: 'add-article',
      component: AddArticle
    },
    {
      path: '/char/add_char',
      name: 'add-char',
      component: AddChar
    },
    {
      path: '/add_user',
      name: 'add-user',
      component: AddUser
    },
    {
      path: '/:user_id',
      name: 'view-user',
      component: ViewUser
    },
    {
      path: '/edit/:user_id',
      name: 'edit-user',
      component: EditUser
    },
    {
      path: '/char/view/:char_id',
      name: 'view-char',
      component: ViewChar
    },
    {
      path: '/games/view/:game_id',
      name: 'view-game',
      component: ViewGame
    }
  ]
})
