<template>
  <div class="manage-articles">
    <vue-headful title="Manage Articles | GameBlitz" />
      <h1 class="grey--text mb-4">Manage Articles</h1>

      <v-container class="my-5">


          <v-card flat v-for="article in articles" :key="article.id">
            <v-layout row wrap class="pa-3">
              <v-flex xs12 md6>
                <div class="caption grey--text">Arcticle Title</div>
                <span class="font-weight-thin">{{ article.article_id }}</span>
                <div>{{ article.title }}</div>
              </v-flex>

              <v-flex xs6 sm4 md2>
                <div class="caption grey--text">Created by</div>
                <v-chip>{{ article.created_by }}</v-chip>
              </v-flex>

              <v-flex xs6 sm4 md2>
                <div class="caption grey--text">Date</div>
                <div>{{ article.date }}</div>
              </v-flex>
            </v-layout>
          </v-card>
      </v-container>
      <!-- floating button -->
      <v-card-text style="position: relative">
        <v-btn absolute dark fab top right color="green" :to="{ name: 'add-article' }">
          <v-icon>add</v-icon>
        </v-btn>
      </v-card-text>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'manage-articles',
  data() {
    return {
      articles: []
    }
  },
  created() {
    db.collection('articles').orderBy('article_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'article_id': doc.data().article_id,
          'title': doc.data().title,
          'content': doc.data().content,
          'created_by': doc.data().created_by,
          'date': doc.data().date,
          'img': doc.data().img,
          'tags': doc.data().data
        }
        this.articles.push(data)
      })
    })
  }
}
</script>

<style lang="sass" scoped>
