<template>
  <div class="edit-user">
    <vue-headful title="Edit Users | GameBlitz" />
      <h1 class="grey--text mb-4">Edit User</h1>

      <v-form @submit.prevent="updateUser">
        <v-container grid-list-md class="pa-5">
          <v-layout wrap>
            <v-flex xs12 sm6 md4>
              <v-text-field name="user_id" label="User Id" v-model="user_id" prepend-icon="fas fa-id-card-alt" readonly></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md8>
              <v-text-field name="name" label="Name" v-model="name" prepend-icon="person"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-select :items="items" v-model="type" label="Type" prepend-icon="fas fa-chalkboard-teacher"></v-select>
            </v-flex>
          </v-layout>
          <!-- button -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" type="submit"><v-icon>save</v-icon>&nbsp;Update</v-btn>
            <v-btn flat color="error" to="/users"><v-icon>cancel</v-icon>&nbsp;Cancel</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'edit-user',
  data() {
    return {
      items: ['Super Admin', 'Admin', 'User'],
      user_id: null,
      name: null,
      type: null,
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('users').where('user_id', '==', to.params.user_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.user_id = doc.data().user_id
          vm.name = doc.data().name
          vm.type = doc.data().type
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('users').where('user_id', '==', this.$route.params.user_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.user_id = doc.data().user_id
          this.name = doc.data().name
          this.type = doc.data().type
        })
      })
    },
    updateUser() {
      db.collection('users').where('user_id', '==', this.$route.params.user_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            user_id: this.user_id,
            name: this.name,
            type: this.type
          })
          .then(() => {
            this.$router.push({name: 'view-user', params: { user_id: this.user_id }})
          })
        })
      })
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
