<template>
  <div class="home">
    <vue-headful title="Home | GameBlitz" />
      <h1 class="grey--text mb-4">Dashboard</h1>

    <!-- carousel -->
    <v-flex xs12 class="mb-5">
      <div class="display-1 grey--text mb-2 font-weight-regular text-uppercase">What's New</div>
      <v-carousel hide-delimiters>
        <v-carousel-item v-for="(cItem, i) in caroItems" :key="i" :src="cItem.src"></v-carousel-item>
      </v-carousel>
    </v-flex>
    <!-- carousel -->

    <div class="display-1 grey--text mb-2 font-weight-regular text-uppercase">Gaming News</div>
    <v-flex xs12 class="mb-3" v-for="article in articles" :key="article.id">
      <v-card>
        <v-img :src="article.img" height="220" width="1775"></v-img>

        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">{{ article.title }}</h3>
            <v-layout row wrap>
              <div>{{ article.content | snippet }}</div>
            </v-layout>
          </div>
        </v-card-title>
      </v-card>
    </v-flex>
  </div>
</template>

<script>
import db from './../components/firebaseInit'

export default {
  name: 'dashboard',
  components: {

  },
  data() {
    return {
      articles: [],
      caroItems: [
        { src: 'https://www.gamewallpapers.com/wallpapers_slechte_compressie/wallpaper_halo_infinite_02_1920x1080.jpg' },
        { src: 'https://www.gamewallpapers.com/wallpapers_slechte_compressie/01wallpapers/wallpaper_cyberpunk_2077_01_1920x1080.jpg' },
        { src: 'https://www.gamewallpapers.com/wallpapers_slechte_compressie/wallpaper_sekiro_shadows_die_twice_01_1920x1080.jpg' }
      ]
    }
  },

  //db
  created() {
    db.collection('articles').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'article_id': doc.data().article_id,
            'title': doc.data().title,
            'content': doc.data().content,
            'created_by': doc.data().created_by,
            'date': doc.data().date,
            'img': doc.data().img
          }
          this.articles.push(data)
      })
    })
  }
}
</script>

