<template>
  <div class="characters">
    <vue-headful title="Characters | GameBlitz" />
      <h1 class="grey--text mb-4">Characters</h1>

      <v-container class="my-5">

        <!-- tools -->
        <v-layout row wrap class="mb-3">
          <v-tooltip top>
            <v-btn small flat color="grey" @click="sortBy('name')" slot="activator">
              <v-icon left small>person</v-icon>
              <span class="caption text-lowercase">by Name</span>
            </v-btn>
            <span>Sort by Characters name</span>
          </v-tooltip>

          <v-tooltip top>
            <v-btn small flat color="grey" @click="sortBy('appeared')" slot="activator">
              <v-icon left small>folder</v-icon>
              <span class="caption text-lowercase">by Appearance</span>
            </v-btn>
            <span>Sort on where they appeared</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <div class="right">
            <v-text-field
              label="Search"
              :append-icon="search?'close':'search'"
              :append-icon-cb="() => (search = '')"
              @keydown.native.escape="search=''"
              single-line
              v-model="search"
              color="grey--text"
              ></v-text-field>
          </div>
        </v-layout>
        <!-- tools -->

        <!-- vcard contents -->
        <v-card flat v-for="character in paginatedData" :key="character.id" :search="search">
          <v-layout row wrap :class="`pa-3 project ${character.platform}`">

            <v-flex xs12 md4>
              <div class="caption grey--text">Name</div>
              <v-chip>
                <v-avatar>
                  <img :src="character.avatar" :alt="character.name" />
                </v-avatar>
                <span>{{ character.name }}</span>
              </v-chip>
            </v-flex>

            <v-flex xs6 sm4 md4>
              <div class="caption grey--text">Appeared</div>
              <div>{{ character.appeared }}</div>
            </v-flex>

            <v-flex xs6 sm4 md2>
              <div class="caption grey--text">Platform</div>
              <v-chip small :class="`${character.platform} white--text caption my-2`">{{ character.platform }}</v-chip>
            </v-flex>

            <v-flex xs2 sm4 md2>
              <div class="right">
                <v-tooltip top>
                  <v-btn flat slot="activator" :to="{ name: 'view-char', params: { char_id: character.char_id }}">
                    <v-icon>visibility</v-icon>
                  </v-btn>
                  <span>View Character</span>
                </v-tooltip>

                <v-tooltip top>
                  <v-btn flat slot="activator">
                    <v-icon>edit</v-icon>
                  </v-btn>
                  <span>Edit</span>
                </v-tooltip>
              </div>
            </v-flex>

          </v-layout>
        </v-card>
        <!-- vcard contents -->
        <!-- floating button -->
        <v-card-text style="position: relative">
          <v-btn absolute dark fab top right color="green" :to="{ name: 'add-char' }">
            <v-icon>add</v-icon>
          </v-btn>
        </v-card-text>

        <v-btn @click="prevPage" flat :disabled="pageNumber==0">
          <v-icon>chevron_left</v-icon>
        </v-btn>

        <v-btn disable flat v-for="page in pageCount">{{ page }}</v-btn>

        <v-btn @click="nextPage" flat :disabled="pageNumber >= pageCount -1">
          <v-icon>chevron_right</v-icon>
        </v-btn>
      </v-container>

  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'characters',
  data() {
    return {
      characters: [],
      search: '',
      pagination: {},
      pageNumber: 0,
    }
  },
  // fetch data
  created() {
    db.collection('characters').orderBy('char_id', 'desc').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'char_id': doc.data().char_id,
          'name': doc.data().name,
          'appeared': doc.data().appeared,
          'avatar': doc.data().avatar,
          'platform': doc.data().platform,
        }
        this.characters.push(data)
      })
    })
  },
  methods: {
    sortBy(prop) {
      this.characters.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
    },
    // pagination
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
    // paging() {
    //   if (pageNumber > 1) {
    //     return nextPage()
    //   } else {
    //     return prevPage()
    //   }
    // }
  },
  // pagination
  props: {
    size: {
      type: Number,
      required: false,
      default: 5
    }
  },
  computed: {
    pageCount() {
      let l = this.characters.length,
          s = this.size;
      return Math.ceil(l/s);
    },
    paginatedData() {
      const start = this.pageNumber * this.size,
            end = start + this.size;

      return this.characters.slice(start, end);
    }
  }
}
</script>

<style lang="sass" scoped>
  $pink: #e92efb
  $puscha: #ff2079
  $violet: #440bd4
  $dblue: #04005e

  .character.Game
    border-left: 4px solid $pink

  .character.Anime
    border-left: 4px solid $puscha

  .v-chip.Game
    background: $dblue

  .v-chip.Anime
    background: $puscha

  .v-chip.Manga
    background: $pink

  .v-chip.Comics
    background: $violet
</style>
