<template>
  <div class="viewuser">
    <vue-headful title="View Users | GameBlitz" />
      <h1 class="grey--text mb-4">View Users</h1>

      <v-layout row wrap>
        <v-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <h3>{{ name }}</h3>
            </v-card-title>
            <v-card-text>
              <span class="font-weight-thin">{{ user_id }}</span>&nbsp;
              <span>{{ type }}</span>
            </v-card-text>
          </v-card>
        </v-flex>

            <v-btn class="primary right" to="/users" block><v-icon>keyboard_backspace</v-icon>&nbsp;Back</v-btn>
            <v-btn @click="deleteUser" class="error right" block><v-icon>delete</v-icon>&nbsp;Delete</v-btn>
      </v-layout>

      <!-- floating button -->
      <v-card-text style="position: relative">
        <v-btn absolute dark fab top right class="pink mt-5" :to="{ name: 'edit-user', params: { user_id: user_id }}">
          <v-icon>edit</v-icon>
        </v-btn>
      </v-card-text>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'viewuser',
  data() {
    return {
      user_id: null,
      name: null,
      type: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('users').where('user_id', '==', to.params.user_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.user_id = doc.data().user_id
          vm.name = doc.data().name
          vm.type = doc.data().type
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('users').where('user_id', '==', this.$route.params.user_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.user_id = doc.data().user_id
          this.name = doc.data().name
          this.type = doc.data().type
        })
      })
    },
    //delete user
    deleteUser() {
      if(confirm('Are you sure?')) {
        db.collection('users').where('user_id', '==', this.$route.params.user_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/users')
          })
        })
      }
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
