<template>
  <div class="view-game">
    <vue-headful title="View Game | GameBlitz" />
      <h1 class="grey--text mb-4">View Game</h1>

      <v-container fluid>
        <v-layout row wrap>

          <v-flex xs12 md2>
            <v-layout row wrap>

              <!-- game cover -->
              <v-flex d-flex>
                <v-card class="ma-2" width="240px">
                    <v-img :src="cover"/>

                  <v-card-title>
                    <div>
                      <span class="subheading">Developer</span><br>
                      <span class="caption grey--text" v-for="devi in dev">{{ devi }}</span><br>
                    </div>
                  </v-card-title>

                  <v-tooltip bottom>
                    <v-btn color="primary" slot="activator" class="wid">Have it?</v-btn>
                    <span>Click it if u have it</span>
                  </v-tooltip>
                </v-card>
              </v-flex>

              <!-- game details -->
              <v-flex d-flex>
                <v-card class="ma-2" width="240px">

                  <v-card-title>
                    <div>
                      <span class="subheading">Developer(s)</span><br>
                      <span class="caption grey--text" v-for="devi in dev">{{ devi }}<br></span><br>
                      <span class="subheading">Publisher(s)</span><br>
                      <span class="caption grey--text" v-for="publ in pub">{{ publ }}<br></span><br>
                      <span class="subheading">Platform(s)</span><br>
                      <span class="caption grey--text" v-for="plat in platform">{{ plat }}<br></span><br>
                      <span class="subheading">Genre(s)</span><br>
                      <span class="caption grey--text" v-for="gen in genre">{{ gen }}<br></span>
                    </div>
                  </v-card-title>
                </v-card>
              </v-flex>

            </v-layout>
          </v-flex>

          <v-flex xs12 md10>
            <v-layout row wrap>

              <!-- title -->
              <v-flex d-flex>
                <v-card class="ma-2">
                  <fbtn class="right" />
                  <v-card-title>
                    <div>
                      <span class="headline">{{ title }}</span><br>
                      <span class="subheading grey--text" v-for="(publish, index) in pub" v-if="index < 1">{{ publish }} | {{ rel_date }}</span><br>
                      <v-rating readonly v-model="rate" color="grey darken-3" empty-icon="$vuetify.icons.ratingFull" half-increments hover></v-rating>
                    </div>
                  </v-card-title>
                </v-card>
              </v-flex>

              <!-- m-content -->
              <v-flex d-flex>
                <v-card class="ma-2" width="200vh">
                  <v-img :src="bg" class="bg-prop"/>
                  <v-card-title>
                    <div>
                      <span class="headline">Description</span>
                      <br><br>
                      <span class="subheading grey--text text--darken-3" v-html="desc">{{ desc }}</span><br>
                    </div>
                  </v-card-title>
                </v-card>
              </v-flex>

              <v-flex d-flex>
                <v-card class="ma-2" width="200vh">
                  <v-card-title>
                    <div>
                      <span class="headline">Plot</span><br><br>
                    </div>
                  </v-card-title>
                </v-card>
              </v-flex>
            </v-layout>
          </v-flex>

        </v-layout>
      </v-container>
  </div>
</template>

<script>
import db from './../firebaseInit'
import fbtn from './../GameBtnFunc.vue'

export default {
  name: 'view-game',
  components: { fbtn },
  data() {
    return {
        game_id: null,
        title: null,
        rel_date: null,
        dev: [null],
        pub: null,
        platform: null,
        genre: null,
        cover: '',
        rate: null,
        desc: null,
        bg: '',
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('games').where('game_id', '==', to.params.game_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.game_id = doc.data().user_id
          vm.title = doc.data().title
          vm.genre = doc.data().genre
          vm.rel_date = doc.data().rel_date
          vm.pub = doc.data().pub
          vm.dev = doc.data().dev
          vm.platform = doc.data().platform
          vm.cover = doc.data().cover
          vm.rate = doc.data().rate
          vm.desc = doc.data().desc
          vm.bg = doc.data().bg
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('games').where('game_id', '==', this.$route.params.game_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.game_id = doc.data().user_id
          this.title = doc.data().title
          this.genre = doc.data().genre
          this.rel_date = doc.data().rel_date
          this.pub = doc.data().pub
          this.dev = doc.data().dev
          this.platform = doc.data().platform
          this.cover = doc.data().cover
          this.desc = doc.data().desc
          this.rate = doc.data().rate
          this.bg = doc.data().bg
        })
      })
    },
    deleteGame() {
      if (confirm('Are you sure?')) {
        db.collection('game').where('game_id', '==', this.$route.params.game_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/games')
          })
        })
      }
    },
  },
}
</script>

<style lang="sass" scoped>
  .accent--text
    color: grey !important

  .wid
    width: 94%

  .bg-prop
    height: 200px
</style>
