<template>
  <div class="add-article">
    <vue-headful title="Create Article | GameBlitz" />
      <h1 class="grey--text mb-4">Create Article</h1>

      <v-form @submit.prevent="saveArticle">
        <v-container grid-list-md class="pa-5">
          <v-layout wrap>
            <v-flex xs12 sm6 md4>
              <v-text-field label="Article Id" v-model="article_id" prepend-icon="fas fa-id-card-alt"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md8>
              <v-text-field label="Title" v-model="title" prepend-icon="person"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-textarea label="Content" v-model="content" prepend-icon="far fa-newspaper"></v-textarea>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-select :items="userItem" label="Created By" v-model="created_by" prepend-icon="person"></v-select>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-text-field label="Date" v-model="date" prepend-icon="far fa-calendar-alt"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-text-field label="Image" v-model="img" prepend-icon="fas fa-image"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-select :items="itemsTag" v-model="tags" label="Type" prepend-icon="fas fa-tags"></v-select>
            </v-flex>
          </v-layout>
          <!-- button -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" type="submit"><v-icon>save</v-icon>&nbsp;Create</v-btn>
            <v-btn flat color="error" to="/man_articles"><v-icon>cancel</v-icon>&nbsp;Cancel</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'add-article',
  data() {
    return {
      userItem: [ 'Markie', 'Kochi', 'Katie Kate' ],
      itemsTag: [ 'New Game', 'Some Random Stuffs', 'PlayStation', 'Nintendo Switch', 'Xbox' ],
      article_id: null,
      title: null,
      content: null,
      created_by: null,
      date: null,
      img: null,
      tags: null,
    }
  },
  methods: {
    saveArticle() {
      db.collection('articles').add({
        article_id: this.article_id,
        title: this.title,
        content: this.content,
        created_by: this.created_by,
        date: this.date,
        img: this.img,
        tags: this.tags,
      })
      .then(docRef => this.$router.push('/man_articles'))
      .catch(error => console.log(err))
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
