<template>
  <div class="add-user">
    <vue-headful title="Add Users | GameBlitz" />
      <h1 class="grey--text mb-4">Add User</h1>

      <v-form @submit.prevent="saveUser">
        <v-container grid-list-md class="pa-5">
          <v-layout wrap>
            <v-flex xs12 sm6 md4>
              <v-text-field name="user_id" label="User Id" v-model="user_id" prepend-icon="fas fa-id-card-alt"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md8>
              <v-text-field name="name" label="Name" v-model="name" prepend-icon="person"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-select :items="items" v-model="type" label="Type" prepend-icon="fas fa-chalkboard-teacher"></v-select>
            </v-flex>
          </v-layout>
          <!-- button -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" type="submit"><v-icon>save</v-icon>&nbsp;Create</v-btn>
            <v-btn flat color="error" to="/users"><v-icon>cancel</v-icon>&nbsp;Cancel</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>

  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'add-user',
  data() {
    return {
      items: ['Super Admin', 'Admin', 'User'],
      user_id: null,
      name: null,
      type: null,
    }
  },
  methods: {
    saveUser() {
      db.collection('users').add({
        user_id: this.user_id,
        name: this.name,
        type: this.type
      })
      .then(docRef => this.$router.push('/users'))
      .catch(error => console.log(err))
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
