<template>
  <div class="games">
    <vue-headful title="Games | GameBlitz" />
      <h1 class="mb-4">Games</h1>

      <v-container grid-list-md>

          <v-card-text style="position: relative">
            <v-btn fixed dark fab top right color="green" :to="{ name: 'add-game' }" class="marg-btn">
              <v-icon>add</v-icon>
            </v-btn>
          </v-card-text>

          <v-layout row wrap>
            <v-flex xs12 md8>
              <v-tooltip right>
                <v-btn small flat color="grey" @click="sortBy('pub')" slot="activator">
                  <v-icon left small>publish</v-icon>
                  <span class="caption text-lowercase">by Publisher</span>
                </v-btn>
                <span>Sort by Game's Publisher</span>
              </v-tooltip>

              <v-tooltip right>
                <v-btn small flat color="grey" @click="sortBy('title')" slot="activator">
                  <v-icon left small>title</v-icon>
                  <span class="caption text-lowercase">by Title</span>
                </v-btn>
                <span>Sort by Game's Title</span>
              </v-tooltip>

              <v-tooltip right>
                <v-btn small flat color="grey" @click="sortDate('rel_date')" slot="activator">
                  <v-icon left small>date_range</v-icon>
                  <span class="caption text-lowercase">by Date</span>
                </v-btn>
                <span>Sort by Games Release date from oldest to newest</span>
              </v-tooltip>

              <v-tooltip right>
                <v-btn small flat color="grey" @click="toggleHave(true)" slot="activator">
                  <v-icon left small>local_mall</v-icon>
                  <span class="caption text-lowercase">by All titles bought</span>
                </v-btn>
                <span>Sort by Game's Title that I have</span>
              </v-tooltip>
            </v-flex>

            <v-flex xs12 md4>
              <v-form>
                <v-text-field
                  label="Search"
                  :append-icon="search?'close':'search'"
                  :append-icon-cb="() => (search = '')"
                  @keydown.native.escape="search=''"
                  single-line
                  v-model="search"
                  color="grey--text"
                  class="caption text-lowercase"
                  ></v-text-field>
              </v-form>
            </v-flex>
          </v-layout>

          <v-layout row wrap>
            <v-hover v-for="game in filterGames" :key="game.id">
              <v-card slot-scope="{ hover }" class="ma-2" width="240px">
                <router-link :to="{ name: 'view-game', params: { game_id: game.game_id }}">
                  <v-img :src="game.cover" :alt="game.name">
                    <v-expand-transition>
                      <div v-if="hover" style="height: 100%;" class="d-flex transition-fast-in-fast-out black v-card--reveal display-3 white--text pa-3">
                        <span class="display-1 cen font-weight-thin">see more details</span>
                      </div>
                    </v-expand-transition>
                  </v-img>
                </router-link>

                <v-card-title>
                  <div>
                      <span class="subheading">{{ game.title }}</span><br>
                    <span class="caption grey--text" v-for="(plat, index) in game.platform" v-if="index < 2">{{ plat }}<br></span>
                  </div>
                </v-card-title>
              </v-card>
            </v-hover>
          </v-layout>
      </v-container>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'games',
  data() {
    return {
      games: [],
      search: '',
    }
  },
  //fetch data
  created() {
    db.collection('games').orderBy('game_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'game_id': doc.data().game_id,
          'title': doc.data().title,
          'rel_date': doc.data().rel_date,
          'platform': doc.data().platform,
          'genre': doc.data().genre,
          'dev': doc.data().dev,
          'pub': doc.data().pub,
          'cover': doc.data().cover,
          'have': doc.data().have
        }
        this.games.push(data)
      })
    })
  },
  methods: {
    sortBy(prop) {
      this.games.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
    },
    sortDate(prop) {
      return this.games.sort((a,b) => new Date(a[prop]) - new Date(b[prop]))
    },
    toggleHave(bought) {
      return this.games = this.games.filter(game => {
         return game.have === bought;
      })
    }
  },
  computed: {
    filterGames() {
      return this.games.filter((game) => {
         return game.title.toLowerCase().match(this.search)// || game.pub.toLowerCase().match(this.search)
      })
    },
  }
}
</script>

<style lang="sass" scoped>
  .marg-btn
    margin-top: 3%
    margin-right: 3%

  a
    text-decoration: none !important

  .v-card--reveal
    align-items: center
    bottom: 0
    justify-content: center
    opacity: .5
    position: absolute
    width: 100%

  .cen
    text-align: center
</style>
