<template>
    <div class="navbar">
      <v-navigation-drawer :mini-variant="miniVariant" fixed app dark class="bg" v-if="viewNav">
        <v-list>
          <v-list-tile-title class="title mb-3">
              <span class="font-weight-light display-1">Game</span>
              <span class="display-1 white--text">Blitz</span>
          </v-list-tile-title>
          <v-list-tile v-for="(item, i) in items" :key="i" :to="item.to" router exact>
            <v-list-tile-action>
              <v-icon small>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.title" />
            </v-list-tile-content>
          </v-list-tile>

          <!-- nested -->
          <v-list-group prepend-icon="fas fa-user-shield">
            <template v-slot:activator>
              <v-list-tile>
                <v-list-tile-title>Admin</v-list-tile-title>
              </v-list-tile>
            </template>

            <v-list-tile v-for="(admin, i) in admins" :key="i" :to="admin.to" router exact>
              <v-list-tile-title small>{{ admin.title }}</v-list-tile-title>
              <v-list-tile-action>
                <v-icon small>{{ admin.icon }}</v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>

          <v-list-tile>
            <v-btn icon @click.stop="miniVariant = !miniVariant">
              <v-icon>{{ `chevron_${miniVariant ? 'right' : 'left'}` }}</v-icon>
            </v-btn>
          </v-list-tile>

        </v-list>
      </v-navigation-drawer>
    </div>
</template>

<script>
export default {
  name: 'navbar',
  data() {
    return {
      miniVariant: true,
      viewNav: true,
      items: [
        {
          icon: 'fas fa-dice',
          title: 'Dashboard',
          to: '/'
        },
        {
          icon: 'videogame_asset',
          title: 'Articles',
          to: '/articles'
        },
        {
          icon: 'fas fa-address-card',
          title: 'About',
          to: '/about'
        },
      ],
      admins: [
        { icon: 'fas fa-users', title: 'Manage Users', to: '/users' },
        { icon: 'fas fa-puzzle-piece', title: 'Manage Articles', to: '/man_articles' },
        { icon: 'fas fa-users', title: 'Characters', to: '/char' },
        { icon: 'fas fa-gamepad', title: 'Games', to: '/games' },
      ],
    }
  },
  // this will watch if the route goes to either dashboard or login, the nav will hide
  watch: {
    $route: function() {
      if (this.$route.path === "/login") {
        return this.viewNav = false
      } else {
        return this.viewNav = true
      }
    }
  }
}
</script>

<style lang="sass" scoped>
  .v-navigation-drawer.bg
    background-color: #1d1d1d

  .v-list__tile--active
    background-color: rgba(17, 175, 104, 0.3)

  .title
    text-align: center
    padding: 32px
    color: #011212
</style>
