<template>
  <div class="view-char">
    <vue-headful title="Characters Profile | GameBlitz" />
      <h1 class="grey--text mb-4">Characters Profile</h1>

      <v-layout row wrap>
        <v-flex xs12>
          <v-card>
            <v-img src=""></v-img>
          </v-card>
        </v-flex>
      </v-layout>
  </div>
</template>

<script>
import db from './../firebaseInit'
export default {
  name: 'view-char',
  data() {
    return {
      characters: [],
    }
  },
  getData() {
    db.collection('characters').orderBy('char_id', 'desc').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'char_id': doc.data().char_id,
          'name': doc.data().name,
          'appeared': doc.data().appeared,
          'avatar': doc.data().avatar,
          'platform': doc.data().platform,
        }
        this.characters.push(data)
      })
    })
  },
  methods: {
    updateId(){
      //this.char
    }
  },
  watch: {
    $route: 'updateId'
  }
}
</script>

