<template>
  <div class="users">
    <vue-headful title="Users | GameBlitz" />
      <h1 class="grey--text mb-4">Users</h1>

      <v-layout row wrap>
        <v-flex xs12 v-for="user in users" :key="user.id">
          <v-card>
            <v-card-title primary-title class="font-weight-medium headline">{{ user.name }}</v-card-title>
            <v-card-text>
              <span class="font-weight-thin">{{ user.user_id }}</span>&nbsp;
              <span>{{ user.type }}</span>

              <v-card-actions>
                <!-- for button function -->
                <div class="right">
                  <v-btn flat :to="{ name: 'view-user', params: { user_id: user.user_id } }" class="ma-0 pa-0">
                    <v-icon>fas fa-eye</v-icon>
                  </v-btn>
                </div>
              </v-card-actions>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

      <!-- floating button -->
      <v-card-text style="position: relative">
        <v-btn absolute dark fab top right color="green" :to="{ name: 'add-user' }">
          <v-icon>add</v-icon>
        </v-btn>
      </v-card-text>

  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  data() {
    return {
      users: []
    }
  },
  created() {
    db.collection('users').orderBy('user_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'user_id': doc.data().user_id,
          'name': doc.data().name,
          'type': doc.data().type
        }
        this.users.push(data)
      })
    })
  }
}
</script>

