<template>
  <div class="add-char">
    <vue-headful title="Add Character | GameBlitz" />
      <h1 class="grey--text mb-4">Add Character</h1>

      <v-form @submit.prevent="saveChar" ref="form">
        <v-container grid-list-md class="pa-5">
          <v-layout wrap>
            <v-flex xs12 sm6 md4>
              <v-text-field label="Character Id" v-model="char_id" prepend-icon="fas fa-id-card-alt" :rules="charIdRule"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md8>
              <v-text-field label="Name" v-model="name" prepend-icon="person" :rules="nameRule"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md6>
              <v-select :items="platItems" label="Platform" v-model="platform" prepend-icon="fab fa-flipboard"></v-select>
            </v-flex>
            <v-flex xs12 sm4 md6>
              <v-text-field label="Avatar" v-model="avatar" prepend-icon="fas fa-image"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Appeared" v-model="appeared" prepend-icon="fas fa-tags"></v-text-field>
            </v-flex>
          </v-layout>
          <!-- button -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" type="submit" :loading="loading"><v-icon>save</v-icon>&nbsp;Create</v-btn>
            <v-btn flat color="error" to="/char"><v-icon>cancel</v-icon>&nbsp;Cancel</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'add-char',
  data() {
    return {
      platItems: ['Game', 'Anime', 'Movie', 'Manga', 'Comics'],
      char_id: '',
      name: '',
      platform: '',
      avatar: '',
      appeared: '',
      // set rules
      charIdRule: [v => v.length >= 4 || 'Minimum length is 4 characters'],
      nameRule: [v => v.length >= 3 || 'Minimum length is 3 characters'],
      loading: false,
    }
  },
  methods: {
    saveChar() {
      if (this.$refs.form.validate()) {
        this.loading = true
        const addChar = {
          char_id: this.char_id,
          name: this.name,
          platform: this.platform,
          avatar: this.avatar,
          appeared: this.appeared
        }

        db.collection('characters').add(addChar).then(() => {
          this.loading = false
        })
        .then(docRef => this.$router.push('/char'))
      }
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
