<template>
  <v-speed-dial
    :direction="direction"
    :open-on-hover="hover"
    :transition="transition"
  >
    <template v-slot:activator>
      <v-btn fab dark small color="teal">
        <v-icon>add</v-icon>
      </v-btn>
    </template>
      <v-btn fab dark small color="indigo">
        <v-icon>edit</v-icon>
      </v-btn>
      <v-btn fab dark small color="red" @click="delGame">
        <v-icon>delete</v-icon>
      </v-btn>
  </v-speed-dial>
</template>

<script>
import db from './firebaseInit'

export default {
  data: () => ({
    direction: 'left',
    hover: true,
    tabs: null,
    transition: 'slide-y-reverse-transition'
  }),
  methods: {
    delGame() {
      if (confirm('Are you sure?')) {
        db.collection('games').where('game_id', '==', this.$route.params.game_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/games')
          })
        })
      }
    }
  }
}
</script>

<style lang="sass" scoped>
  .v-speed-dial
    position: relative
</style>
